import React, { Component } from "react";
import Header from "../structure/Header";
import Footer from "../structure/Footer";
import Menu from "../structure/Menu";
import TableFavorites from "../Tables/TableFavorites";
import WidgetNumberFavortites from "../Widgets/NumberFavorites";
import WidgetNumberCategories from "../Widgets/NumberCategories";
import WidgetNumberteams from "../Widgets/NumberTeams";
import WidgetNumberNacionalities from "../Widgets/NumberNationalities";
import Loader from "../Loader/Loader";
import { FormattedMessage } from 'react-intl';


const API_KEY = `${process.env.REACT_APP_API_TRAIL_KEY}`;
const API_URL = `${process.env.REACT_APP_API_TRAIL_URL}`;
const TOKEN = API_KEY;
var EventID = 0;
var arrayFavorites = [];
export default class Favorites extends Component {
  constructor(props) {
    super(props);
    this.state = {
      athleteList: [],
      loading: false,
    };

    this.getAthletsList();
  }

  componentWillMount() {
    try {
      if (JSON.parse(localStorage.getItem(`athleteList${this.props.match.params.EventID}`))) {
        const athList = JSON.parse(localStorage.getItem(`athleteList${this.props.match.params.EventID}`))
        this.setState({ athleteList: athList }, () => this.checkFavorites());

      }

      else {
        this.getAthletsList();

      }
    }
    catch{
      localStorage.clear();

    }}
        /*List of athlets*/
    getAthletsList = async e => {
      EventID = this.props.match.params.EventID;
      const API_CALL = await fetch(`${API_URL}/trail/Athletes/${EventID}/`, {
        headers: new Headers({
          Authorization: TOKEN
        })
      });
      const data = await API_CALL.json();

      this.setState({ athleteList: data }, () => this.checkFavorites());
    };

    /*Check Favorites*/
    checkFavorites() {
      arrayFavorites = []
      this.state.athleteList.map(athlete => {
        if (parseInt(athlete.athlete.athlete_id) === parseInt(localStorage.getItem(athlete.athlete.athlete_id))) {
          arrayFavorites.push(athlete)
        }
        return null
      })
      this.setState({ loading: true })
    }

    renderTable() {
      if (this.state.loading === true) {
        if (arrayFavorites.length > 0) {
          return (
            <TableFavorites
              athleteList={arrayFavorites}
              loading={this.state.loading}
              eventID={EventID}
              competitionID={0} />
          )


        }
        else
          return (
            <p>
              < FormattedMessage id="NOFAVORITES" />
            </p>
          )
      }
    }

    render() {
      return (
        <div>
          <Header eventID={this.props.match.params.EventID} />
          {/*Send the event id to the MENU, to get the competition list*/}
          <Menu eventID={EventID} competitionID={0} />
          {/*CONTENT*/}
          <div>
            {/* Content Wrapper. Contains page content */}
            <div className="content-wrapper">

              {/* Content Header (Page header) */}
              <section className="content-header">
                <h1>
                  < FormattedMessage id="FAVORITESLIST" />
                </h1>
              </section>
              {/* Main content */}
              {/*Check if data is loading*/}
              {(() => {
                if (this.state.loading === false) {
                  return (
                    <section className="content">
                      <div className="row">
                        <div className="col-xs-12">
                          <div className="box">
                            {/* /.box-body */}
                            <div className="box-body" style={{ display: 'flex', justifyContent: 'center' }}>
                              <Loader />
                            </div>

                          </div>
                        </div>
                      </div>
                    </section>
                  )
                }
                else {
                  return (

                    <section className="content">
                      <div className="row">
                        {/* widget for the number of participants */}
                        <WidgetNumberFavortites athleteList={arrayFavorites} />
                        {/* widget for the number of Teams */}
                        <WidgetNumberteams athleteList={arrayFavorites} />
                        {/* widget for the number of Categories */}
                        <WidgetNumberCategories athleteList={arrayFavorites} />
                        {/* widget for the number of Nacionalities */}
                        <WidgetNumberNacionalities athleteList={arrayFavorites} />
                      </div>
                      <div className="row">
                        <div className="col-xs-12">
                          <div className="box">
                            {/* /.box-body */}
                            <div className="box box-primary"></div>
                            <div className="post" style={{ textAlign: "center" }}>
                              {this.renderTable()}
                            </div>

                          </div>
                          {/* Table for the list of participants */}


                        </div>
                      </div>
                      {/* /.col */}
                      {/* /.row */}

                    </section>
                  )
                }
              })()}
            </div>
            {/* /.content-wrapper */}
          </div>
          <Footer eventID={this.props.match.params.EventID} />
        </div >
      );
    }

  }
